.header {
  
  width:  100%;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #77002e;
  padding: 0 10%;
}

.logo {
  font-size: 2rem;
  color: white;
  font-weight: bold;
  text-align: center;

  background-image: linear-gradient(black, purple, rgb(106, 163, 153),white);
  color: transparent;
background-clip: text;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.header li {
  margin-left: 3rem;
}

.header a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #fcb8d2;
}

.header a:hover,
.header a:active,
.header a.active {
  color: white;
}

a.visited.active{
    color: red;
}
.badge {
  background-color: #cc2062;
  color: white;
  border-radius: 12px;
  padding: 0 1rem;
  margin-left: 0.5rem;
}